<template>
  <div class="order-wrap">
    <h3></h3>
    <div class="order-list-choose">
      <div class="order-list-option">
        
        <v-selection :selections="products" @on-change="productChange"></v-selection>
      </div>

      <div class="order-list-option">
        
        <v-date-picker @on-change="getStartDate"></v-date-picker>
      </div>

      <div class="order-list-option">
        
        <v-date-picker @on-change="getEndDate"></v-date-picker>
      </div>

      <div class="order-list-option">
        
        <input type="text" v-model.lazy="query" class="order-query">
      </div>
    </div>
    <div class="order-list-table">
      <table>
        <tr>
          <th v-for="head in tableHeads" @click="changeOrderType(head)" :class="{active:head.active}">{{ head.label }}</th>
        </tr>
        <tr v-for="item in tableData" :key="item.period">
          <td v-for="head in tableHeads">{{ item[head.key] }}</td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
import VSelection from '../components/base/selection'
import VDatePicker from '../components/base/datepicker'
import _ from 'lodash'
export default {
  components: {
    VSelection,
    VDatePicker
  },
  data () {
    return {
      tableData: [],
          // tableData: [
          //   {
          //     "orderId": "ddj123",
          //     "product": "",
          //     "version": "",
          //     "period": "1",
          //     "buyNum": 2,
          //     "date": "2016-10-10",
          //     "amount": "500"
          //   },
          //   {
          //     "orderId": "yuj583",
          //     "product": "",
          //     "version": "",
          //     "period": "3",
          //     "buyNum": 1,
          //     "date": "2016-5-2",
          //     "amount": "2200"
          //   },
          //   {
          //     "orderId": "pmd201",
          //     "product": "",
          //     "version": "",
          //     "period": "3",
          //     "buyNum": 12,
          //     "date": "2016-8-3",
          //     "amount": "7890"
          //   }
          // ],
      query: '',
      productId: 0,
      startDate: '',
      endDate: '',
      products: [
        {
          label: '',
          value: 0
        },
        {
          label: '',
          value: 1
        },
        {
          label: '',
          value: 2
        },
        {
          label: '',
          value: 3
        }
      ],
      tableHeads: [
        {
          label: '',
          key: 'orderId'
        },
        {
          label: '',
          key: 'product'
        },
        {
          label: '',
          key: 'version'
        },
        {
          label: '',
          key: 'period'
        },
        {
          label: '',
          key: 'date'
        },
        {
          label: '',
          key: 'buyNum'
        },
        {
          label: '',
          key: 'amount'
        }
      ],
      currentOrder: 'asc',
    }
  },
  // watch: {
  //   query () {
  //     this.getList()
  //   }
  // },
  methods: {
    productChange (obj) {
      this.productId = obj.value
      this.getList()
    },
    getStartDate (date) {
      this.startDate = date
      this.getList()
    },
    getEndDate (date) {
      this.endDate = date
      this.getList()
          alert(this.tableData[0].orderId)
    },
    // getList () {
    //   let reqParams = {
    //     query: this.query,
    //     productId: this.productId,
    //     startDate: this.startDate,
    //     endDate: this.endDate
    //   }
    //   this.$http.post('http://localhost:8081/getOrderList', reqParams)
    //   .then((res) => {
    //     this.tableData = res.data.list
    //   }, (err) => {

    //   })
    // },
    changeOrderType (headItem) {
      this.tableHeads.map((item) => {
        item.active = false
        return item
      })
      headItem.active = true
      if (this.currentOrder === 'asc') {
        this.currentOrder = 'desc'
      }
      else if (this.currentOrder === 'desc') {
        this.currentOrder = 'asc'
      }
      this.tableData = _.orderBy(this.tableData, headItem.key, this.currentOrder)
    }
  },
  mounted () {
    this.$store.dispatch('fetchOrderList')
    console.log(tableData)
  },
  computed: {
    tableData() {
      return this.$store.getters.getOrderList
    }
  }
}

</script>

<style scoped>
.order-wrap {
  width: 1200px;
  min-height: 800px;
  margin: 20px auto;
  overflow: hidden;
}
.order-wrap h3 {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 20px;
}
.order-query {
  height: 25px;
  line-height: 25px;
  border: 1px solid #e3e3e3;
  outline: none;
  text-indent: 10px;
}
.order-list-option {
  display: inline-block;
  padding-left: 15px;
}
.order-list-option:first-child {
  padding-left: 0;
}
.order-list-table {
  margin-top: 20px;
}
.order-list-table table {
  width: 100%;
  background: #fff;
}
.order-list-table td,
.order-list-table th {
  border: 1px solid #e3e3e3;
  text-align: center;
  padding: 10px 0;
}
.order-list-table th {
  background: #4fc08d;
  color: #fff;
  border: 1px solid #4fc08d;
  cursor: pointer;
}
.order-list-table th.active {
  background: #35495e;
}
</style>



